  display: flex;
}

.base-timer {
  width: 300px;
  height: 300px;
  position: relative;
}

.base-timer__svg {
  transform: scaleX(-1);
}

.base-timer__circle {
  fill: none;
  stroke: none;
}

.base-timer__path-elapsed {
  stroke-width: 7px;
  stroke: #000;
}

.base-timer__path-remaining {
  stroke-width: 7px;
  stroke-linecap: round;
  transform-origin: center;
  fill-rule: nonzero;
  stroke: currentColor;
  transition: all 1s linear;
  transform: rotate(90deg);
}

.base-timer__path-remaining.green, .base-timer__path-remaining.orange {
  color: #fff;
}

.base-timer__path-remaining.red {
  color: #000;
}

.base-timer__label {
  width: 300px;
  height: 300px;
  justify-content: center;
  align-items: center;
  font-size: 200px;
  display: flex;
  position: absolute;
  top: 0;
}

